/***********************************
Mobile Game List Page Styling
************************************/

/* colors used and thier css variables:
  #fdfdff    or var(--white)
  #ddd       or var(--gray)
  #02558b    or var(--primary-color)
  #2cf       or var(--secondary-color)
  #ff533d    or var(--action-color)
*/

.GameList {
  background-color: #fdfdff;
  padding-bottom: 6rem;
  padding-top: 4rem;
  position: relative;
}

.GameList .material-icons {
  margin-right: 0rem;
}

.each_game {
  background-color: transparent;
  border: none;
  /* border-radius: 3px; */
  /* box-shadow: 1px 2px 2px 2px rgba(0, 0, 0, 0.2); */
  margin: 1rem 1.2rem;
  min-height: 2rem;
  padding: 10px;
  padding-left: 0px;

  /* border-top: 1px solid white; */
}

.each_game .card-title {
  color: var(--secondary-color);
}

.each_game a {
  display: -ms-flexbox;
  display: flex;
}

.game_initial {
  -ms-flex-align: center;
      align-items: center;
  background-color: #02558b;
  /* background-color: transparent; */
  border-radius: 50%;
  -webkit-box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.2);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 3rem;
      flex-basis: 3rem;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 0rem 2.5rem 0rem 1rem;
}

.game_initial i {
  color: white;
}

.game_initial #card {
  transform: rotate(180deg);
  /* Safari */
  -webkit-transform: rotate(180deg);
  /* Firefox */
  -moz-transform: rotate(180deg);
  /* IE */
  -ms-transform: rotate(180deg);
  /* Opera */
  -o-transform: rotate(180deg);
}

.game_title {
  color: #2cf;
  margin-bottom: 0px;

  /* color: white; */
}

.game_category {
  color: darkgray;
  font-style: italic;
  margin-bottom: 0px;

  /* color: white; */
}

.click-arrow {
  -ms-flex-item-align: center;
      align-self: center;
  color: var(--action-color);
  margin-left: auto;
}

.click-arrow i {
  font-size: 30px;
  vertical-align: middle;

  /* color: white; */
}

.FAB {
  bottom: 5rem;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 100%;
}

.FAB a {
  background: -webkit-linear-gradient(45deg, var(--action-color) 35%, #CC200F);
  background: -o-linear-gradient(45deg, var(--action-color) 35%, #CC200F);
  background: linear-gradient(45deg, var(--action-color) 35%, #CC200F);
  border-radius: 18px;
  -webkit-box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.2);
  color: var(--white);
  display: -ms-flexbox;
  display: flex;
  margin: 0 auto;
  padding: 0rem 0.85rem;
  -ms-flex-align: center;
      align-items: center;
}

.FAB p {
  margin-bottom: 0;
  border-right: 2px solid var(--white);
  padding-right: 0.5rem;
  padding: 0.35rem 0.5rem 0.35rem 0rem;
}

.FAB div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin-left: 0.2rem;
  padding: 0.35rem 0rem;
}

@media screen and (min-width: 800px) {

  .games-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin: 0rem auto;
    width: 85%;
  }

  .each_game {
    -ms-flex: 0 0 22%;
        flex: 0 0 22%;
    padding: 0;
    text-align: center;
    -webkit-transition: -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
    transition: -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
    -o-transition: transform 0.3s, box-shadow 0.3s;
    transition: transform 0.3s, box-shadow 0.3s;
    transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
  }

  .each_game a {
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 1rem;
  }

  .each_game:hover {
    -webkit-box-shadow: 3px 3px 3px 0px rgba(0,0,0,0.4);
            box-shadow: 3px 3px 3px 0px rgba(0,0,0,0.4);
    -webkit-transform: scale(1.03);
        -ms-transform: scale(1.03);
            transform: scale(1.03);
    -webkit-transition: -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
    transition: -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
    -o-transition: transform 0.3s, box-shadow 0.3s;
    transition: transform 0.3s, box-shadow 0.3s;
    transition: transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
  }

  .each_game:hover a {
    text-decoration: none;
  }

  .game_initial {
    width: 24%;
    margin: 0rem auto;
  }

  .game_initial i {
    /* width: 100%; */
  }

  .click-arrow {
    margin: 0;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
  }



}



